import React from "react";

export default function TechStack() {
    return(
        <div id="tech">
            <div className="techStack">
            <h1>Tech Stack</h1>
            <div className="tech-container">
                <div className="tech">
                    <img src="./images/html-color-icon.png" alt="html-icon"/>
                    <p>HTML</p>
                </div>
                <div className="tech">
                    <img src="./images/css-color-icon.png" alt="css-icon"/>
                    <p>CSS</p>
                </div>
                <div className="tech">
                    <img src="./images/javascript-color-icon.png" alt="javascript-icon"/>
                    <p>JavaScript</p>
                </div>
                <div className="tech">
                    <img src="./images/react-color-icon.png" alt="react-icon"/>
                    <p>React.js</p>
                </div>
                <div className="tech">
                    <img src="./images/git-color-icon.png" alt="github-icon"/>
                    <p>GitHub</p>
                </div>
            </div>
            
        </div>

        </div>
        
        
    )
}